body {
  margin: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  cursor: url('./components/img/icons/Cursor.png'), auto;	
  background-color: #191B1E;
  color: white;
  font-family: 'Cinzel', serif;
  overflow-x: hidden;
}
::-moz-selection { /* Code for Firefox */
  color: #FFE84F;
  background: black;
}

::selection {
  color: #FFE84F;
  background: black;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
p{
  margin: 0;
  font-size: 13px;
}
ul{
  padding: 0;
  margin: 0;
  list-style: none;
}

.all{
  width: 100%;
  position: relative;
  overflow-x: hidden;
}

.back{
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 150vh;
  background: radial-gradient(at 10% 10% ,#434447,#191B1E , #191B1E);
  background-repeat: no-repeat;
  
}
.top{
  z-index: -1;
}