.contact{
    background-color: rgba(0, 255, 255, 0);
    display: flex;
    align-items: center;
    justify-content: center;
}
.contact-in{
    display: grid;
    grid-template-rows: 20% 80%;
    border: 1px solid rgba(255, 0, 0, 0);
    background-color: rgba(0, 255, 255, 0);
    width: 600px;
    height: 500px;
}
.contact-in .contact-head{
    color: #EEF6F8;
    font-weight: bold;
    font-size: 50px;
}
.contact-form form{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    background-color: rgba(0, 255, 255, 0);
    width: 100%;
    height: 100%;
}
.contact-form .row{
    display: grid;
    grid-template-columns: 25% 75%;
    gap: 7px;
    background-color: rgba(0, 255, 255, 0);
    width: 100%;
}
.text-input{
    opacity: 0.5;
}
.contact-title{
    background-color: rgba(0, 255, 255, 0);
    display: block;
    text-align: right;
    font-size: 30px;
}
.send-row{
    display: flex;
    justify-content: flex-end;
    width: 100%;
}
.send-row .send{
    color: #EEF6F8;
    background-color: rgb(42, 42, 42);
    border: 1px solid #EEF6F8;
    font-family: 'Cinzel', serif;
    font-weight: bold;
    text-align: center;
    width: 115px;
    height: 45px;
    font-size: 18px;
}