.what-i{
    background-color: rgb(0, 0, 0);
    display: grid;
    grid-template-columns: auto;
    min-height: 100vh;
    padding-bottom: 15vh;
    position: relative;
}
.tra{
    position: absolute;
    top: -10vh;
    width: 100%;
    height: 10vh;
    /* background-color: rgb(255, 99, 71); */
    background-image: linear-gradient(1deg , black 50%, #191b1e00 50%);
}
.what-i-para{
    width: 271px;
    height: 100%;
}
.what-head{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-evenly;
    background-color: rgba(0, 255, 255, 0);
    height: 50vh;
    
}
.what-head div:nth-child(1){
    font-size: 40px;
    color: #eef6f8e3;
}
.what-head div:last-child{
    font-size: 15px;
    color: #eef6f8b2;
    font-family: 'Raleway', sans-serif;
    font-weight: 100;
    
}
.what-body{
    display: grid;
    justify-items: center;
}
.what-body .para-one{
    font-size: 25px;
    color: rgba(255, 255, 255, 0.897);
    
}
.what-body .para-two{
    font-size: 16px;
    color: rgba(255, 255, 255, 0.733);
}
.what-body .para-three{
    font-size: 12px;
    color: rgba(255, 255, 255, 0.411);
}
.what-i-para div{
    margin-top: 10px;
}
@media screen and (max-width: 768px) {
    .what-body{
        grid-template-columns: auto;
    }
    .what-head div:last-child{
        width: 250px;
    }
    .what-head{
        align-items: center;
    }
    .what-i-para{
        margin-top: 50px;
    }
    .tra{
        background-image: linear-gradient(10deg , black 50%, #191b1e00 50%);
    }
}
@media screen and (min-width: 769px) and (max-width: 1090px) {
    .what-body{
        grid-template-columns: 50% 50%;
    }
    .what-head div:last-child{
        width: 650px;
    }
    .what-i-para{
        margin-top: 50px;
    }
    .what-i{
        padding-bottom: 25vh;
        padding-left: 50px;
    }
    .tra{
        background-image: linear-gradient(3deg , black 50%, #191b1e00 50%);
    }
}
@media screen and (min-width: 1091px)  {
    .what-body{
        grid-template-columns: 25% 25% 25% 25%;
    }
    .what-head div:last-child{
        width: 750px;
    }
    .what-i{
        padding-bottom: 10vh;
        padding-left: 50px;
    }
}